html {
	min-height: 100%;
	position: relative;
	-webkit-overflow-scrolling: touch;
	overflow-scrolling: touch;
}

* {
	font-family: inherit;
	-webkit-font-smoothing: antialiased;
}

.slick-slide,
a,
img,
button {
	outline: none !important;
	border: 0;
}

img,
svg,
canvas,
object {
	max-width: 100%;
	height: auto;
}

a {
	text-decoration: none;
	transition: all .3s $trans;
}

a:focus,
a:hover {
	text-decoration: none;
	color: #000000;
}

p:first-child {
	margin-top: 0;
}

p:last-child {
	margin-bottom: 0;
}

p+p {
	margin-top: 7px;
}

::-moz-selection {
	background: $yellow;
	color: $black;
}

::selection {
	background: $yellow;
	color: $black;
}

/*
::-webkit-scrollbar {
